.login-form-container {
    width: 400px;
    margin-top: 100px;
    padding: 40px;
    border: 1px solid #ccc;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

/* @media screen and (max-width: 760px) {
    .login-form-container {
      width: 300px;
    }
  } */


.login-form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.login-form input[type="text"],
.login-form input[type="password"] {
    width: 280px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-family: "omyu-pretty";
    font-size: medium;
    margin-bottom: 10px;
}

.login-btns {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
}

.login-btns button {
    width: 300px;
    padding: 10px;
    border-radius: 4px;
    font-family: "omyu-pretty";
    font-size: medium;
    margin-bottom: 10px;
    cursor: pointer;

}

.login-btn {
    border: none;
    background-color: rgb(178, 222, 240);
}

.sign-btn {
    background-color: rgb(252, 253, 231);
    border: 1.5px solid rgb(140, 216, 247);
}

.login-btns button:hover {
    background-color: rgb(140, 216, 247);
}

.google-login {
    display: flex;
    justify-content: center;
}

.icon-google {
    background-image: url(data:image/png;base64,);
    /* background-repeat: no-repeat; */
    background-size: 100%;
    display: block;
    height: 20px;
    margin-right: 10px;
    width: 20px;
}

.hr-line {
    width: 300px;
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

.hr-line hr {
    border: 1px solid #ccc;
    padding: 0;
    width: 43%;
}

.hr-line span {
    color: #ccc;
}

.oauth-login-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 85%;
    height: 50px;
    margin-bottom: 15px;

    border: none;
    border-radius: 5px;
    cursor: pointer;

}

.naver-login {
    background-color: #03C75A;
}

/* 네이버 로그인 로고 */
.naver-login > .oauth-login-img {
    width: 10%;
    margin: 0 10px;
}

.naver-login > p {
    color: rgb(255, 255, 255);
    font-size: 16px;
    font-weight: 800;
    line-height: 160%;
    letter-spacing: -0.64px;
}

.kakao-login {
    background-color: #FEE500;
}

/* 카카오톡 로그인 로고 */
.kakao-login > .oauth-login-img {
    width: 5%;
    margin: 0 10px;
}

.kakao-login > p {
    font-size: 16px;
    font-weight: 800;
    line-height: 160%;
    letter-spacing: -0.64px;
}