.memo {
    height: 1000px;
  }
  
  .writer {
    display: flex;
    font-weight: bold;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  .writer > img {
    width: 15px;
    margin-right: 5px;
  }

  .m-content {
    display: none;
  }
  
  .empty-message {
    margin-top: 20px;
    color: white;
  }
  
  .note.square {
    /* background-color: #fcf8e3; */
    border-radius: 6px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1);
    margin: 10px;
    padding: 10px;
    width: 100px;
    height: 100px;
    /* display: inline-block; */
    /* position: relative; */
    /* overflow: hidden; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
  }

  .note.square.beige {
       background-color: #fcf8e3;
  }


  .note.square.pink {
    background-color: pink;
}

.note.square.skyblue {
  background-color: rgb(178, 222, 240);
}
  
  @media screen and (max-width: 760px) {
    .note.square {
      width: 50px;
      height: 50px;
      font-size: small;
    }
  }
  
  .note.heart {
    position: relative;
    width: 150px;
    height: 135px;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
  }
  
  @media screen and (max-width: 760px) {
    .note.heart {
      width: 100px; 
      height: 90px;
      font-size: small;
    }
  }
  
  .note.heart::before,
  .note.heart::after {
    content: "";
    position: absolute;
    top: 0;
    width: 75px;
    height: 120px;
    /* background-color: pink; */
    border-radius: 50px 50px 0 0;
    z-index: -1;
  }

  .note.heart.pink::before,
  .note.heart.pink::after {
  background-color: pink;
}

.note.heart.beige::before,
  .note.heart.beige::after {
  background-color: #fcf8e3;
}

.note.heart.skyblue::before,
  .note.heart.skyblue::after {
  background-color: rgb(178, 222, 240);
}



  
  @media screen and (max-width: 760px) {
    .note.heart::before,
    .note.heart::after {
      width: 45px; 
      height: 75px; 
    }
  }
  
  
  .note.heart::before {
    left: 75px;
    transform: rotate(-45deg);
    transform-origin: 0 100%;
  }
  
  @media screen and (max-width: 760px) {
    .note.heart::before {
      left: 45px; 
    }
  }
  
  
  .note.heart::after {
    left: 0;
    transform: rotate(45deg);
    transform-origin: 100% 100%;
  }
  
  
  .note.heart .writer {
    margin-top: -20px;
    width: 100px;
  }
  
  @media screen and (max-width: 760px) {
    .note.heart .writer {
      margin-top: -15px; 
      margin-right: 10px;
      width: 70px;
    }
  }
  
