  .btns {
    margin-bottom: 50px;
  }
  
  .btn {
    padding: 10px 15px;
    font-size: medium;
    margin: 5px 10px;
    background-color: #4C2A2A;
    border: 1px solid #4C2A2A;
    border-radius: 5px;
    color: white;
    font-family: "omyu-pretty";
